import React, { useState, useEffect } from 'react'
import {Panel, PanelHeader, HeaderButton, platform, IOS} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { ListItem, Button, Group, Div, Avatar, FixedLayout, Tabs, TabsItem } from '@vkontakte/vkui';
import './style.css'

const osname = platform();

const Default = props => {

    const [timer, setTimer] = useState();
    const [active, setActive] = useState('main');
    const [items, setItems] = useState(JSON.parse(localStorage.getItem('items')));
    const [prices, setPrices] = useState(JSON.parse(localStorage.getItem('prices')));
    const [score, setScore] = useState(parseInt(localStorage.getItem('score')));
    const [data, setData] = useState(JSON.parse(localStorage.getItem('data')));

    useEffect(() => {
        setInterval(() => {
            autoAdd();
        },1000);
    }, []);

    const add = () => {
        saveScore(data.perClick);
    }
  
  // Cброс счёта
  /* const resetScore = (obj) => {
    for (var prop in obj) {
        if (obj.hasOwnProperty(prop)) {
            obj[prop] = { string: '', number: 0 }[typeof obj[prop]];
        }
    }
  } */

  // Автоматическое добавление в секунду
  const autoAdd = () => {
    if(typeof score !== 'object') {
        saveScore(data.auto);
        setTimer(!timer);
    }
  }

  const getScore = () => {
      return parseInt(localStorage.getItem('score'));
  }

  const saveScore = ( additional ) => {
    setScore(getScore() + additional);
    localStorage.setItem('score', getScore() + additional);
  }

  const buy = (item) => {
    if(getScore() - prices[item].price > 0) {
        saveScore(-prices[item].price);
        setItems(Object.assign(items,{ [item]: items[item] + 1 }));
        setPrices(Object.assign(prices, { [item]: Object.assign(prices[item], { price: Math.round(prices[item].price * 1.1)})}));
        setData(Object.assign(data,{ auto: data.auto + prices[item].boost }));
        localStorage.setItem('items', JSON.stringify(items));
        localStorage.setItem('prices', JSON.stringify(prices));
        localStorage.setItem('data', JSON.stringify(data));
    } else {
        console.log('no money');
    }
}
    return (
      <Panel id={props.id}>
        		<PanelHeader>
                    Clicker
		        </PanelHeader>
                <Group>
                        <Div className="score-container">
                            <p>{score}#</p>
                        </Div>    
                </Group>
                <Group>
                    <Div className="button-container">
                        <button className="button-click" onClick={() => add()}>
                            <h1>H+</h1>
                            <span>{data.auto} / сек</span>
                        </button>
                    </Div>
                </Group>
                {Object.keys(items).map((item) => (
                    <Group>
                        <Div onClick={() => buy(item)}>
                             {item} - {items[item]} | +{prices[item].boost}/с             
                        </Div>
                    </Group>
                ))
                }
                <FixedLayout vertical="bottom">
              <Tabs>
                <TabsItem
                  onClick={() => setActive('main')}
                  selected={active === 'main'}
                >Главная</TabsItem>
                <TabsItem
                  onClick={() => setActive('shop')}
                  selected={active === 'shop'}
                >Магазин</TabsItem>
              </Tabs>
            </FixedLayout>    
      </Panel>
    )
}
  
  export default Default
